import { Reducer } from 'redux';
import * as types from './actionTypes';
import { AppAction, ShopDetailSchema } from '../types';

export interface ShopDetailStore {
  [id: number]: ShopDetailSchema;
}

const shopDetailReducer: Reducer<ShopDetailStore, AppAction> = (
  state = {},
  action
) => {
  switch (action.type) {
    case types.FETCH_SHOP_FULFILLED:
      const { shopDetail } = action.payload;
      return { ...state, [shopDetail.id]: shopDetail };
    default:
      return state;
  }
};

export default shopDetailReducer;
