import { AppLatLng } from './store/types';
import moment from 'moment';

function deg2rad(deg: number) {
  return deg * (Math.PI / 180);
}

function sqr(n: number) {
  return n * n;
}

export function distance(latlng1: AppLatLng, latlng2: AppLatLng) {
  const R = 6371; // Radius of the earth in km
  const dLat = deg2rad(latlng2.lat - latlng1.lat);
  const dLon = deg2rad(latlng2.lng - latlng1.lng);
  const a =
    sqr(Math.sin(dLat / 2)) +
    Math.cos(deg2rad(latlng1.lat)) *
      Math.cos(deg2rad(latlng2.lat)) *
      sqr(Math.sin(dLon / 2));
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return R * c;
}

