import React, { FC, useEffect } from 'react';
import { RouteComponentProps } from 'react-router';
import PageFrame from 'templates/PageFrame';
import './style.scss';
import { MenuList } from '@material-ui/core';
import { ReduxStore } from 'store/types';
import { useSelector, useDispatch } from 'react-redux';
import ShopInfoCard from 'components/ShopInfoCard';
import { queryDB } from 'store/Bookmark/actions';

const BookmarkPage: FC<RouteComponentProps> = props => {
  const dispatch = useDispatch();
  const { shopIds, shops } = useSelector((state: ReduxStore) => state.bookmark);
  useEffect(() => {
    dispatch(queryDB('shops', ...shopIds));
  }, []);

  return (
    <PageFrame title='收藏' {...props}>
      <MenuList>
        {shops.map(shop => {
          return (
            <ShopInfoCard
              key={shop.id}
              shop={shop}
              bookmarked={true}
              refLatLng={null}
            />
          );
        })}
      </MenuList>
    </PageFrame>
  );
};

export default BookmarkPage;
