import * as types from './actionTypes';
import { action } from 'typesafe-actions';
import { ShopState } from 'store/types';

export const toggleBookmark = (shopId: number) =>
  action(types.TOGGLE_BOOKMARK, { shopId });

export const addBookmark = (...shopIds: number[]) =>
  action(types.ADD_BOOKMARK, { shopIds });

export const removeBookmark = (...shopIds: number[]) =>
  action(types.REMOVE_BOOKMARK, { shopIds });

export const queryDB = (storeName: string, ...keys: number[]) =>
  action(types.QUERY_DB, { storeName, keys });

export const queryDBFulfilled = (result: ShopState[]) =>
  action(types.QUERY_DB_FULFILLED, result);

export const queryDBRejected = (error: any) =>
  action(types.QUERY_DB_REJECTED, undefined, undefined, error);
