import * as types from './actionTypes';
import * as actions from './actions';
import { call, put, takeLatest } from 'redux-saga/effects';
import { SagaIterator } from 'redux-saga';
import { getByKeys } from 'localDB';

export function* queryDB(
  action: ReturnType<typeof actions.queryDB>
): SagaIterator {
  const { storeName, keys } = action.payload;
  try {
    const shops = yield call(getByKeys, storeName, ...keys);
    yield put(actions.queryDBFulfilled(shops));
  } catch (e) {
    yield put(actions.queryDBRejected(e));
  }
}

function* watchQueryDB() {
  yield takeLatest(types.QUERY_DB, queryDB);
}

export default [watchQueryDB];
